      <i class="fas fa-plus-square"></i> Êñ∞Â¢ûË™≤Á®ã
    </button>

    <table class="admin-table">
      <thead>
        <tr>
          <th class="d-none-phone">ÂúñÁ§∫</th>
          <th class="d-none-phone">È°ûÂà•</th>
          <th>Ë™≤Á®ãÂêçÁ®±</th>
          <th class="d-none-phone">Á≤æÈÅ∏</th>
          <th>Ââ©È§òÊï∏Èáè</th>
          <th class="d-none-tab-p">ÂéüÂÉπ</th>
          <th>ÂîÆÂÉπ</th>
          <th>Á∑®ËºØ</th>
        </tr>
      </thead>

