<script>
import Pagination from '@/components/Pagination.vue';

export default {
  name: 'CoursesAdmin',
  components: { Pagination },
  computed: {
    isLoading() {
      const vm = this;
      return vm.$store.state.isLoading;
    },
    textCounter() {
      const vm = this;
      return vm.course.title;
    },
    courses() {
      const vm = this;
      return vm.$store.state.courses.courses;
    },
  },
  methods: {
    deleteCourse(title, id) {
      const vm = this;
      vm.$modal.show('dialog', {
        title: `‚ö†Ô∏è Á¢∫ÂÆöÂà™Èô§ ${title} ÂóéÔºü`,
        text: 'Ë™≤Á®ã‰∏ÄÊó¶Âà™Èô§Â∞áÁÑ°Ê≥ïÂæ©Âéü üò±üò±üò±',
        buttons: [
          {
            title: 'Á¢∫ÂÆö',
            handler: () => {
              vm.$store.dispatch('courses/removeCourse', id).then(() => {
                vm.$toasted.success(`ÊàêÂäüÂà™Èô§ ${title}`, {
                  duration: 2000,
                });
                vm.$modal.hide('dialog');
              });
            },
          },
          {
            title: 'ÂèñÊ∂à',
          },
        ],
      });
    },

    addCourse() {
      const vm = this;
      vm.$store
        .dispatch('courses/createCourse', { ...vm.course })
        .then(() => {
          vm.$toasted.success('ÊàêÂäüÊñ∞Â¢ûË™≤Á®ã', {
            duration: 2000,
          });
        });
    },

    updateCourse(id) {
      const vm = this;
      vm.$store
        .dispatch('courses/updateCourse', { id, course: vm.course })
        .then(() => {
          vm.$toasted.success('Êõ¥Êñ∞ÊàêÂäü!!', {
            duration: 2000,
          });
        });
    },
    // Á∑®ËºØ/Êñ∞Â¢ûË™≤Á®ã
    turnNewEditPage(status, course) {
      const vm = this;
      vm.$store.commit('courses/setStatus', status);
      if (status === 'Á∑®ËºØË™≤Á®ã') {
        vm.$store.commit('courses/setCourse', course);
      } else {
        vm.$store.commit('courses/setCourse', {});
      }
      vm.$router.push({ name: 'courseNewEdit' });
    },
  },

  created() {
    const vm = this;
    vm.$store.dispatch('courses/getCoursesLimit');
    vm.$store.dispatch('courses/getPageLength');
  },
};
</script>

